* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

@font-face {
  font-family: "Montserrat";
  src: url("../assets/Montserrat-Bold.ttf") format("truetype");
  font-display: swap;
}

@font-face {
  font-family: "Libre Baskerville";
  src: url("../assets/LibreBaskerville-Regular.ttf") format("truetype");
  font-display: swap;
}

@font-face {
  font-family: "Niconne";
  src: url("../assets/Niconne-Regular.ttf") format("truetype");
  font-display: swap;
}

hr {
  width: 80vw;
}

/* mobile navbar */

#hamburger-link {
  display: none;
}

.navbar-open {
  width: 100vw;
  opacity: 1;
  height: 55vh;
  visibility: visible;
  background-color: #111;
}

.navbar-closed {
  opacity: 0;
  width: 0;
  height: 0;
  visibility: hidden;
  font-family: "Libre Baskerville", serif;
}

.mobile-navbar {
  display: none;
  flex-direction: column;
  background-color: #111;
  background-color: #121212;
  display: flex;
  flex-direction: column;
  text-align: left;
}

.mobile-navbar a {
  color: #fff;
  text-decoration: none;
  font-family: "Libre Baskerville", serif;
}

#hamburger-link span {
  font-size: 7rem;
  color: #fff;
}

#close-btn {
  position: absolute;
  right: 0;
}

.mobile-subnav {
  float: left;
  overflow: hidden;
}

.mobile-menu-links {
  display: flex;
  flex-direction: column;
  line-height: 2rem;
  align-items: flex-start;
  margin-left: 10vw;
  padding-top: 4vh;
}

.mobile-menu-links a {
  margin-right: 35vw;
}

.mobile-subnav-btn {
  background-color: transparent;
  color: #fff;
  border: none;
  font-size: 1.1rem;
  font-family: "Libre Baskerville", serif;
}

.mobile-subnav-content {
  display: none;
  text-align: left;
  padding-left: 5vw;
}

.mobile-subnav-content hr {
  width: 75vw;
}

.mobile-subnav:hover .mobile-subnav-content {
  display: block;
}

.mobile-subnav-content a {
  display: flex;
  flex-direction: column;
}

.navbar-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: #111;
}

.navbar {
  overflow: hidden;
  padding-top: 3vh;
  padding-left: 2vw;
}

.navbar a {
  color: white;
  text-decoration: none;
  font-family: "Libre Baskerville", serif;
  float: left;
  padding: 14px 16px;
}

.navbar a:hover {
  text-decoration: underline;
}

.subnav {
  float: left;
  overflow: hidden;
}

.subnav .subnav-btn {
  font-size: 1.3rem;
  border: none;
  outline: none;
  color: white;
  padding: 0.7rem 0.5rem 1.3rem 0.5rem;
  background-color: inherit;
  font-family: inherit;
  margin: 0;
}

.subnav-content {
  display: none;
  position: absolute;
  left: 0;
  background-color: #99752c;
  width: 100%;
  z-index: 1;
}

.subnav-content a {
  float: left;
  color: white;
  text-decoration: none;
}

.subnav-content a:hover {
  background-color: #111;
  color: #99752c;
}

.subnav:hover .subnav-content {
  display: block;
}

.main-logo {
  color: white;
  text-decoration: none;
  font-size: 3rem;
  font-family: "Niconne", cursive;
  padding-top: 3vh;
  line-height: 3rem;
}

.facebook-btn {
  width: 6vw;
  height: 6vh;
}

.instagram-btn {
  width: 6vw;
  height: 6vh;
}

.social-media-btns {
  padding-top: 3vh;
  padding-right: 2vw;
}

#map {
  height: 50vh;
}

/* contact info section */
.contact-info-section {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  padding-top: 20vh;
  padding-bottom: 20vh;
}

.contact-info-flex {
  margin-top: 12vh;
}

.address {
  font-family: "Libre Baskerville", serif;
  display: flex;
  margin-left: 0.5vw;
}

.phone-number-section {
  display: flex;
  flex-direction: row;
}

.email-section {
  display: flex;
  flex-direction: row;
}

.how-to-reach-us {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.phone-and-email {
  margin-top: 10vh;
}

.phone-icon {
  padding-right: 1vw;
}

.email-icon {
  padding-right: 1vw;
}

.how-to-reach-us-header {
  font-size: 2rem;
  color: #751b05;
  font-family: "Montserrat", sans-serif;
}

.contact-links {
  text-decoration: none;
  color: #111;
  font-size: 1.2rem;
  padding-left: 1vw;
}

.contact-links:hover {
  text-decoration: underline;
  cursor: pointer;
}

.hours {
  display: flex;
  flex-direction: column;
}

.hours-header {
  margin-bottom: 12vh;
  font-size: 2rem;
  color: #751b05;
  font-family: "Montserrat", sans-serif;
}

.hours-section {
  font-size: 1.5rem;
  line-height: 2.5rem;
}

.day-flex {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.day {
  margin-right: 5vw;
}

/* footer */
#footer {
  background-color: #111;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 4vh;
  padding-bottom: 8vh;
  line-height: 3rem;
}

.main-footer-links {
  display: flex;
  justify-content: space-around;
  width: 25vw;
}

.main-footer-links a {
  font-size: 1rem;
  color: #fff;
  text-decoration: none;
  font-family: "Libre Baskerville", serif;
}

.footer-subnav a {
  color: #fff;
  text-decoration: none;
  font-family: "Libre Baskerville", serif;
}

.footer-subnav {
  display: flex;
  justify-content: space-around;
  width: 30vw;
}

.footer-phone-number:hover {
  color: #99752c;
}

.bottom-footer {
  background-color: #99752c;
  text-align: center;
  color: #fff;
  line-height: 5vh;
}
